.wrap {
  margin-top: 48px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px 12px 30px 20px;
  width: 686px;
  background-color: #eaeaea;
  border-radius: 12px;
}

.info {
  color: #949292;
}

.day {
  font-size: 32px;
}

.date {
  font-size: 16px;
}

.logs {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.log {
  display: flex;
  background-color: #c4c4c4;
  border-radius: 12px;
  width: 428px;
}

.log__time {
  margin-left: 16px;
  font-size: 32px;
  color: #000000;
}

.log__text {
  margin-left: 45px;
  font-size: 32px;
  color: #ffffff;
}
