.wrap {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  gap: 48px;
}

.wrap button {
  width: 131px;
  height: 38px;

  box-sizing: border-box;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  background-color: #c4c4c4;
  border-radius: 12px;

  color: #000000;
  font-size: 32px;

  cursor: pointer;

  border: none;

  transition-duration: 250ms;
}

.wrap .focus {
  background-color: #d5f2ff;
}

.wrap button:hover {
  transform: scale(1.1);
}